.video-item {
  display: flex !important;
  align-items: center;
  cursor: pointer;
}

.video-item.item img {
  max-width: 180px;
}

@media screen and (max-width: 992px) {
  .video-item.item img {
    max-width: 100%;
  }
  .video-item {
    flex-direction: column;
  }
  .video-item .content {
    padding-left: 0 !important;
  }
}

@media screen and (max-width: 767px) {
  .video-item.item img {
    max-width: 180px;
  }
  .video-item {
    flex-direction: row;
    align-items: center;
  }
  .video-item .content {
    margin: 0 0 0 0.5em;
  }
}
